import { Card, CardImg, CardBody, CardTitle, Button, Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Input } from "reactstrap";
import { useState, useEffect } from "react";
import { useNavigate } from "react-router";
import axios from "axios";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";
import { getcourse, deletecourse } from "../../api/user";
const Grid = () => {
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [deleteIndex, setDeleteIndex] = useState(null);
  const [apiData, setApiData] = useState([]);
  const [filteredData, setFilteredData] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    async function callApi() {
      try {
        const apiResult = await getcourse();
        console.log(apiResult)
        setApiData(apiResult);
        setFilteredData(apiResult);
      } catch (error) {
        console.log(error);
      }
    }
    callApi();
  }, []);

  const toggleDeleteModal = (index) => {
    setDeleteIndex(index);
    setShowDeleteModal(!showDeleteModal);
  };

  const handleDelete = async () => {
    try {
      const deletedCourseId = filteredData[deleteIndex].id;
      await deletecourse(deletedCourseId);
      setApiData(apiData.filter((course) => course.id !== deletedCourseId));
      setFilteredData(filteredData.filter((course) => course.id !== deletedCourseId));
    } catch (error) {
      console.log(error);
    }
    setShowDeleteModal(false);
  };

  const handleEdit = (actionType, courseId) => {
    if (actionType === "add") {
      navigate("../addcourse", { state: { actionType } });
    } else if (actionType === "edit") {
      navigate("../addcourse", { state: { actionType, courseId } });
    }
  };

  const handleSearch = (query) => {
    setSearchQuery(query);
    const filteredCourses = apiData.filter((course) =>
      course.title.toLowerCase().includes(query.toLowerCase())
    );
    setFilteredData(filteredCourses);
  };
function showdetail(course){
console.log("fucntion")
  navigate("../admincourse",{ state:course })
}
  return (
    <div style={{ marginTop: "60px" }}>
      <div>
        <h4 className="mb-3 caliber-font">Courses</h4>
        <div className="search-bar">
        <Input  style={{marginLeft:"5px" , }}
                    type="text"
                    name="search"
                    id="search"
                    placeholder="Search by name"
                    value={searchQuery}
                    onChange={(e) => handleSearch(e.target.value)}
                  />
        </div>
        <button
          onClick={() => handleEdit("add")}
          className="btn btn-success caliber-font"
          style={{ float: "right", marginTop: "-45px" }}
        >
          Add Course
        </button>
        <Row style={{marginTop:"15px"}}>
          {filteredData.map((course, index) => (
            <Col sm="6" lg="6" xl="3" key={index}>
              <Card style={{height:'90%'}} > 
                <LazyLoadImage onClick={() => showdetail(course)}
                  loading="lazy"
                  src={course.featuredImage}
                  effect="blur"
                  placeholderSrc={course.featuredImage}
                  style={{
                    width: "100%",
                    height: "250px",
                    objectFit: "cover",
                  }}
                />
                <CardBody className="p-4 caliber-font">
                <div style={{height:"35px"}}>  <CardTitle  tag="h5">{course.title}</CardTitle></div>
                
                  
                  <Button onClick={() => handleEdit("edit", course.id)} color={course.color}>
                    Edit
                  </Button>
                  
                  <Button
                    onClick={() => toggleDeleteModal(index)}
                    style={{
                      backgroundColor: "red",
                      color: "white",
                      float: "right",
                      height: "37px",
                      borderRadius: "8px",
                      border: "1px solid red",
                    }}
                  >
                    Delete
                  </Button>
             
                </CardBody>
              </Card>
            </Col>
          ))}
        </Row>
        <Modal isOpen={showDeleteModal} toggle={toggleDeleteModal}>
          <ModalHeader toggle={toggleDeleteModal}>Delete Course</ModalHeader>
          <ModalBody>Are you sure you want to delete this course?</ModalBody>
          <ModalFooter>
            <Button color="danger" onClick={handleDelete}>
              Delete
            </Button>
            <Button color="secondary" onClick={toggleDeleteModal}>
              Cancel
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    </div>
  );
};

export default Grid;
